import React from 'react';
import { Col } from 'rsuite';
import avax from '../assets/img/avax.png';
import bnb from '../assets/img/bnb.png';
import bsc from '../assets/img/bsc.png';
import eth from '../assets/img/eth.png';

var unit;
var logoimg;
var btnState;
function Card(props) {
	function viewPool() {
		window.localStorage.setItem('presaleAddress', props.presaleAddress);
		window.localStorage.setItem('tokenAddress', props.tokenAddress);
		window.localStorage.setItem('tokenName', props.title);
		window.localStorage.setItem('tokenSymbol', props.symbol);
		window.localStorage.setItem('tokenSupply', props.total);
		window.localStorage.setItem('tokenDecimals', props.decimal);
		window.localStorage.setItem('rate', props.rate);
		window.localStorage.setItem('softCap', props.softCap);
		window.localStorage.setItem('hardCap', props.hardCap);
		window.localStorage.setItem('minBuy', props.minBuy);
		window.localStorage.setItem('maxBuy', props.maxBuy);
		window.localStorage.setItem('from', props.from);
		window.localStorage.setItem('to', props.to);
		window.localStorage.setItem('whitelist', props.whitelist);
		window.localStorage.setItem('whitelistState', props.whitelistState);

		window.localStorage.setItem('tokenOwner', props.user);
		window.localStorage.setItem('chainId', props.chainID);
		window.localStorage.setItem('logoUrl', props.logoUrl);
		window.localStorage.setItem('website', props.website);
		window.localStorage.setItem('facebook', props.facebook);
		window.localStorage.setItem('twitter', props.twitter);
		window.localStorage.setItem('github', props.github);
		window.localStorage.setItem('telegram', props.telegram);
		window.localStorage.setItem('instagram', props.instagram);
		window.localStorage.setItem('discord', props.discord);
		window.localStorage.setItem('reddit', props.reddit);
		window.localStorage.setItem('description', props.description);

		window.location.href = `/PadInfo`;
	}

	switch (props.chainID) {
		case 56:
			unit = 'BNB';
			logoimg = bnb;
			break;
		case 97:
			unit = 'tBNB';
			logoimg = bnb;
			break;
		case 1:
			unit = 'ETH';
			logoimg = eth;
			break;
		case 3:
			unit = 'ETH';
			logoimg = eth;
			break;
		case 43114:
			unit = 'AVAX';
			logoimg = avax;
			break;
		default:
			unit = 'BNB';
			logoimg = bnb;
	}

	if (window.localStorage.getItem('chainId') === props.chainID) {
		btnState = true;
	} else {
		btnState = false;
	}

	return (
		<Col>
			<div className="card">
				<img
					src={props.logoUrl}
					style={{ width: '50px', marginLeft: '5px', marginTop: '5px' }}
					alt="logo"
					align="left"
				/>
				<img
					src={logoimg}
					alt="net"
					style={{ width: '30px', marginRight: '5px', marginTop: '5px' }}
					align="right"
				/>
				<div className="card-body text-center p-6 p-lg-5 pt-0 pt-lg-0">
					<div className="content-title">
						<h2>
							<p className="title">
								<span>
									<strong>
										{props.title} ({props.symbol})
									</strong>
								</span>
							</p>
						</h2>
						<p className="subtitle">
							1 {unit} = {props.rate}
							{props.symbol}
						</p>
					</div>
					<div className="soft-hard-cap" style={{ marginTop: '20px' }}>
						<p>
							SoftCap:{' '}
							<span>
								{props.softCap} {unit}{' '}
							</span>
						</p>
						<p>
							HardCap:{' '}
							<span>
								{props.hardCap} {unit}{' '}
							</span>
						</p>
					</div>
					<div className="soft-hard-cap">
						<p>
							MinBuy:{props.minBuy} {unit}
						</p>
						<p>
							MaxBuy:{props.maxBuy} {unit}
						</p>
					</div>
					<div className="soft-hard-cap">
						<p>
							From:
							<strong>
								<span>{props.from}</span>
							</strong>
						</p>
						<p>
							To:
							<strong>
								<span>{props.to}</span>
							</strong>
						</p>
					</div>

					<br />
					<div className="view-pool has-text-right">
						<span className="text-white card-whitelist-text">
							{props.whitelistState ? 'whitelist' : ''}
						</span>
						<button className="card-btn" disabled={btnState} onClick={viewPool}>
							View Pool
						</button>
					</div>
				</div>
			</div>
		</Col>
	);
}

export default Card;
