import { GET_PADS, GET_PAD, PAD_LOADING, GET_WHILTELIST } from "../actions/types.js";

const initialState = {
  pads: [],
  whitelist: [],
  loading: false,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case PAD_LOADING:
      return {
        ...state,
        loading: true,
      };

    case GET_PADS:
      return {
        ...state,
        pads: action.payload,
        loading: false,
      };

    case GET_PAD:
      return {
        ...state,
        pad: action.payload,
        loading: false,
      };
    case GET_WHILTELIST:
      return {
        ...state,
        whitelist: action.payload,
        loading: false
      }
    default:
      return state;
  }
}
