export const getWhitelist = (id) => (dispatch) => {
	dispatch(setPadLoading());
	axios
		.get(`/api/addWhitelist/${id}`)
		.then((whitelists) =>
			dispatch({
				type: GET_WHILTELIST,
				payload: whitelists.data.whiteList
			})
		)
		.catch((err) =>
			dispatch({
				type: GET_WHILTELIST,
				payload: null
			})
		);
};

export const setPadLoading = () => {
	return {
		type: PAD_LOADING
	};
};
