import React, { Component } from 'react';
import classnames from 'classnames';
import Web3 from 'web3';
import axios from 'axios';
import { Button, Input, Modal, Radio, RadioGroup, List } from 'rsuite';
import RemindIcon from '@rsuite/icons/RemindRound';
import { getWhitelist } from '../../actions/padActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {
	FaChrome,
	FaDiscord,
	FaFacebookSquare,
	FaGithub,
	FaInstagram,
	FaReddit,
	FaTelegram,
	FaTwitter
} from 'react-icons/fa';

var netName;
var netNameBtn = true,
	tokenNameBtn = true,
	finalizeBtn = true;

var ownerState = false;

class PadInfo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			buy: '',
			formErrors: { buy: '' },
			buyValid: false,
			formValid: false,
			presaleState: '',
			soldTokens: 0,
			raisedAmount: 0,
			numberBuyer: 0,
			modalState: false,
			whiteListState: false,
			address: '',
			listArray: [],
			adderssSelect: ''
		};

		this.onSubmit = this.onSubmit.bind(this);
		this.onModalState = this.onModalState.bind(this);
		this.addAddress = this.addAddress.bind(this);
		this.onChangeValue = this.onChangeValue.bind(this);
		this.onCancelClick = this.onCancelClick.bind(this);
		this.onFinalizeClick = this.onFinalizeClick.bind(this);
		this.onwithRawTokenName = this.onwithRawTokenName.bind(this);
		this.onwithdrawNetName = this.onwithdrawNetName.bind(this);

		axios
			.get(`/api/getPresaleContractAbi`)
			.then(async (res) => {
				const abi = res.data;
				const web3 = new Web3(Web3.givenProvider);

				const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
				presaleContract.methods.presaleStatus().call().then((res) => {
					switch (res) {
						case '3':
							this.state.presaleState = 'Failed';
							break;
						case '2':
							this.state.presaleState = 'Successful';
							break;
						case '1':
							this.state.presaleState = 'Active';
							break;
						case '0':
							this.state.presaleState = 'Queued';
					}
				});
				presaleContract.methods.status().call().then((res) => {
					this.state.numberBuyer = res.num_buyers;
					this.state.soldTokens = Number(res.sold_amount) / 1000000000000000000;
					this.state.raisedAmount = Number(res.raised_amount) / 1000000000000000000;
					console.log(this.state);
					this.setState({
						numberBuyer: res.num_buyers,
						soldTokens: Number(res.sold_amount) / 1000000000000000000,
						raisedAmount: Number(res.raised_amount) / 1000000000000000000
					});
				});
			})
			.catch((err) => console.log(err));
	}

	componentDidMount() {
		this.props.getWhitelist(window.localStorage.getItem('presaleAddress'));
	}
	onModalState() {
		this.setState({
			modalState: !this.state.modalStat
		});
	}

	changeWhiteListState(value) {
		this.setState({
			whiteListState: value
		});
	}

	onSubmit(e) {
		e.preventDefault();

		if (window.localStorage.getItem('signer')) {
			const userAddress = window.localStorage.getItem('userAddress');

			axios
				.get(`/api/getPresaleContractAbi`)
				.then(async (res) => {
					const abi = res.data;
					const web3 = new Web3(Web3.givenProvider);

					let parameter = {
						from: userAddress,
						value: web3.utils.toWei(this.state.buy, 'ether')
					};
					const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
					presaleContract.methods.userDeposit().send(parameter).then(() => {
						return (
							<Modal
								backdrop="static"
								role="alertdialog"
								open={this.state.modalState}
								onClose={this.onModalState}
								size="xs"
							>
								<Modal.Body>
									<RemindIcon
										style={{
											color: '#ffb300',
											fontSize: 24
										}}
									/>
									input parameter Successful
								</Modal.Body>
								<Modal.Footer>
									<Button onClick={this.onModalState} appearance="primary">
										Ok
									</Button>
								</Modal.Footer>
							</Modal>
						);
					});
				})
				.catch((err) => console.log(err));
		} else {
			alert('You must connect a wallet!');
		}
	}

	handleInput(e) {
		const name = e.target.name;
		const value = e.target.value;
		this.setState({ [name]: value }, () => {
			this.validateField(name, value);
		});
	}

	validateField(fieldName, value) {
		let fieldValidationErrors = this.state.formErrors;

		let buyValid = this.state.buyValid;

		switch (fieldName) {
			case 'buy':
				buyValid =
					value >= window.localStorage.getItem('minBuy') && value <= window.localStorage.getItem('maxBuy')
						? true
						: false;
				fieldValidationErrors.buy = buyValid ? '' : ' is invalid';

				const WLarray = Array(window.localStorage.getItem('whitelist'));
				console.log(WLarray);

				if (
					window.localStorage.getItem('whitelistState') === true &&
					WLarray.includes(window.localStorage.getItem('userAddress'))
				)
					break;
			default:
				break;
		}
		this.setState(
			{
				formErrors: fieldValidationErrors,
				buyValid: buyValid
			},
			this.validateForm
		);
	}

	validateForm() {
		this.setState({ formValid: this.state.buyValid });
	}

	addAddress() {
		const data = {
			launchpadAddress: window.localStorage.getItem('presaleAddress'),
			val: this.state.address
		};
		axios
			.post(`/api/addWhitelist`, data)
			.then((res) => {
				window.localStorage.setItem('whitelist', res);

				this.setState({
					listArray: res
				});

				this.props.getWhitelist(window.localStorage.getItem('presaleAddress'));
				console.log(res);
			})
			.catch((err) => console.log(err));
	}

	onChangeValue(event) {
		this.setState({
			address: event
		});
	}

	onFinalizeClick() {
		if (window.localStorage.getItem('signer')) {
			const userAddress = window.localStorage.getItem('userAddress');
			axios.get(`/api/getPresaleContractAbi`).then(async (res) => {
				const abi = res.data;
				const web3 = new Web3(Web3.givenProvider);

				let parameter = {
					from: userAddress,
					value: web3.utils.toWei(this.state.buy, 'ether')
				};
				const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
				presaleContract.methods.ownerWithdrawTokens().send(parameter);
			});
		}
	}

	onCancelClick() {
		if (window.localStorage.getItem('signer')) {
			const userAddress = window.localStorage.getItem('userAddress');
			axios.get(`/api/getPresaleContractAbi`).then(async (res) => {
				const abi = res.data;
				const web3 = new Web3(Web3.givenProvider);

				let parameter = {
					from: userAddress,
					value: web3.utils.toWei(this.state.buy, 'ether')
				};
				const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
				presaleContract.methods.withdrawICOCoin().send(parameter);
			});
		}
	}

	onwithRawTokenName() {
		if (window.localStorage.getItem('signer')) {
			const userAddress = window.localStorage.getItem('userAddress');
			axios.get(`/api/getPresaleContractAbi`).then(async (res) => {
				const abi = res.data;
				const web3 = new Web3(Web3.givenProvider);

				let parameter = {
					from: userAddress,
					value: web3.utils.toWei(this.state.buy, 'ether')
				};
				const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
				presaleContract.methods.userWithdrawTokens().send(parameter);
			});
		}
	}

	onwithdrawNetName() {
		if (window.localStorage.getItem('signer')) {
			const userAddress = window.localStorage.getItem('userAddress');
			axios.get(`/api/getPresaleContractAbi`).then(async (res) => {
				const abi = res.data;
				const web3 = new Web3(Web3.givenProvider);

				let parameter = {
					from: userAddress,
					value: web3.utils.toWei(this.state.buy, 'ether')
				};
				const presaleContract = new web3.eth.Contract(abi, window.localStorage.getItem('presaleAddress'));
				presaleContract.methods.userWithdrawBaseTokens().send(parameter);
			});
		}
	}

	render() {
		const { whitelist } = this.props.pad;

		if (window.localStorage.getItem('tokenOwner') === window.localStorage.getItem('userAddress')) ownerState = true;
		else ownerState = false;

		var lister;
		if (whitelist.length !== 0) {
			lister = whitelist.map((item, id) => (
				<List.Item key={id} index={id} className="info-list-item">
					{item.whiteListAddress}
				</List.Item>
			));
		}

		switch (window.localStorage.getItem('chainId')) {
			case '1':
				netName = 'ETH';
				break;
			case '3':
				netName = 'ETH';
				break;
			case '56':
				netName = 'BNB';
				break;
			case '97':
				netName = 'tBNB';
				break;
			case '43114':
				netName = 'AVAX';
				break;
			default:
				netName = 'BNB';
				break;
		}

		switch (this.state.presaleState) {
			case 'Successful':
				tokenNameBtn = false;
				finalizeBtn = false;
				break;
			case 'failed':
				netNameBtn = false;
				break;
			default:
				tokenNameBtn = true;
				finalizeBtn = true;
				netNameBtn = true;
				break;
		}

		return (
			<div>
				<section className="ant-layout black-background">
					<main className="ant-layout-content MainLayout_content__2mZF9">
						<div className="py-5">
							<div className="bg-dark style-border columns mt-4">
								<div className="column is-flex-grow-2">
									<div className="ant-card ant-card-bordered">
										<div className="ant-card-body">
											<article className="media pool-detail" style={{ position: 'relative' }}>
												<div className="media-content">
													<div className="content">
														<div className="is-flex is-align-items-center">
															<div>
																{window.localStorage.getItem('website') ? (
																	<a
																		href={window.localStorage.getItem('website')}
																		style={{ marginRight: '5px' }}
																	>
																		<FaChrome size={30} />
																	</a>
																) : (
																	''
																)}
																<a
																	href={window.localStorage.getItem('facebook')}
																	style={{ marginRight: '5px' }}
																>
																	<FaFacebookSquare size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('twitter')}
																	style={{ marginRight: '5px' }}
																>
																	<FaTwitter size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('github')}
																	style={{ marginRight: '5px' }}
																>
																	<FaGithub size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('telegram')}
																	style={{ marginRight: '5px' }}
																>
																	<FaTelegram size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('instagram')}
																	style={{ marginRight: '5px' }}
																>
																	<FaInstagram size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('discord')}
																	style={{ marginRight: '5px' }}
																>
																	<FaDiscord size={30} />
																</a>
																<a
																	href={window.localStorage.getItem('reddit')}
																	style={{ marginRight: '5px' }}
																>
																	<FaReddit size={30} />
																</a>
															</div>
														</div>
														<div className="ant-typography" />
													</div>
												</div>
											</article>
											<div className="table-container mt-6">
												<table>
													<tbody>
														<tr>
															<td>
																<img
																	src={window.localStorage.getItem('logoUrl')}
																	width="60px"
																	height="60px"
																	alt="logo"
																/>
															</td>
															<td className="has-text-right">
																<h3 className="mr-3 info-panel-title">
																	{window.localStorage.getItem('tokenName')} Presale
																</h3>
															</td>
														</tr>
														<tr>
															<td className="pay-text">Description</td>
															<td className="has-text-right">
																{window.localStorage.getItem('description')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Chain</td>
															<td className="has-text-right">
																{window.localStorage.getItem('chainId')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Token Address</td>
															<td className="has-text-right addresses">
																<a
																	href={
																		'https://testnet.bscscan.com/address/' +
																		window.localStorage.getItem('tokenAddress')
																	}
																	target="_blank"
																	rel="noreferrer nofollow"
																>
																	{window.localStorage.getItem('tokenAddress')}
																</a>
															</td>
														</tr>
														<tr>
															<td className="pay-text">Token Name</td>
															<td className="has-text-right">
																{window.localStorage.getItem('tokenName')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Token Symbol</td>
															<td className="has-text-right">
																{window.localStorage.getItem('tokenSymbol')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Token Decimals</td>
															<td className="has-text-right">
																{window.localStorage.getItem('tokenDecimals')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Total Supply</td>
															<td className="has-text-right">
																{window.localStorage.getItem('tokenSupply')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Presale Address</td>
															<td className="has-text-right">
																<a
																	className="mr-1"
																	href={
																		'https://testnet.bscscan.com/address/' +
																		window.localStorage.getItem('presaleAddress')
																	}
																	target="_blank"
																	rel="noreferrer nofollow"
																>
																	{window.localStorage.getItem('presaleAddress')}
																</a>
																<br />
															</td>
														</tr>
														<tr>
															<td className="pay-text">Presale Rate</td>
															<td className="has-text-right">
																1 BNB = {window.localStorage.getItem('rate')}
																{window.localStorage.getItem('symbol')}
															</td>
														</tr>

														<tr>
															<td className="pay-text">Soft Cap</td>
															<td className="has-text-right">
																{window.localStorage.getItem('softCap')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Hard Cap</td>
															<td className="has-text-right">
																{window.localStorage.getItem('hardCap')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Minimum Buy</td>
															<td className="has-text-right">
																{window.localStorage.getItem('minBuy')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Maximum Buy</td>
															<td className="has-text-right">
																{window.localStorage.getItem('maxBuy')}
															</td>
														</tr>

														<tr>
															<td className="pay-text">Presale Start Time</td>
															<td className="has-text-right">
																{window.localStorage.getItem('from')}
															</td>
														</tr>
														<tr>
															<td className="pay-text">Presale End Time</td>
															<td className="has-text-right">
																{window.localStorage.getItem('to')}
															</td>
														</tr>
													</tbody>
												</table>
											</div>
										</div>
									</div>
								</div>
								<div className="column is-flex-grow-1">
									<div className="ant-card ant-card-bordered">
										<div className="ant-card-body">
											<div className="field">
												<table>
													<tbody>
														<tr>
															<td className="pay-text">Presale Status</td>
															<td>{this.state.presaleState}</td>
														</tr>
														<tr>
															<td className="pay-text">Number of Buyers</td>
															<td>{this.state.numberBuyer}</td>
														</tr>
														<tr>
															<td className="pay-text">Sold Tokens</td>
															<td>{this.state.soldTokens}</td>
														</tr>
														<tr>
															<td className="pay-text">Raised Amount</td>
															<td>{this.state.raisedAmount}</td>
														</tr>
													</tbody>
												</table>
												<div className="ant-card ant-card-bordered">
													{ownerState ? (
														<div className="ant-card-body">
															<div className="mt-2">
																<div className="field">
																	<label
																		htmlFor=""
																		className="pay-text"
																		style={{ fontSize: '30px' }}
																	>
																		Sale Type
																	</label>
																	<RadioGroup
																		style={{ width: '100%', display: 'flex' }}
																		value={this.state.whiteListState}
																		onChange={(value) => {
																			this.changeWhiteListState(value);
																		}}
																	>
																		<Radio value={false}>Public</Radio>
																		<Radio value={true}>WhiteList</Radio>
																	</RadioGroup>
																</div>
																{this.state.whiteListState ? (
																	<div>
																		<List autoScroll className="info-list">
																			{lister}
																		</List>
																		<Input
																			placeholder="whitelist address"
																			id="pay-info-input-owner"
																			style={{ marginTop: '10px' }}
																			value={this.state.address}
																			onChange={this.onChangeValue}
																		/>
																		<Button
																			className="pay-info-button"
																			style={{ marginTop: '20px' }}
																			onClick={this.addAddress}
																		>
																			Add
																		</Button>
																	</div>
																) : (
																	''
																)}
																<hr />
															</div>
															<label
																htmlFor=""
																className="label"
																style={{ fontSize: '15px' }}
															>
																Pool Actions
															</label>
															<div className="my-2">
																<button
																	disabled={finalizeBtn}
																	type="button"
																	className="ant-btn ant-btn-default ant-btn-block ant-btn-dangerous pay-info-action-button"
																	onClick={this.onFinalizeClick}
																>
																	<span>Finalize Pool</span>
																</button>
															</div>
															<div className="my-2">
																<button
																	type="button"
																	className="ant-btn ant-btn-default ant-btn-block ant-btn-dangerous pay-info-action-button"
																	onClick={this.onCancelClick}
																>
																	<span>Cancel Pool</span>
																</button>
															</div>
														</div>
													) : (
														''
													)}
												</div>
											</div>
										</div>
									</div>
								</div>
								<div className="column is-flex-grow-1">
									<div className="ant-card ant-card-bordered">
										<div className="ant-card-body">
											<form onSubmit={this.onSubmit}>
												<div className="field">
													<label className="label">
														Amount should be more than{' '}
														{window.localStorage.getItem('minBuy')} {netName} and less than{' '}
														{window.localStorage.getItem('maxBuy')} {netName}
													</label>
													<div style={{ marginTop: '20px' }}>
														<input
															value={this.state.buy}
															onChange={(event) => this.handleInput(event)}
															className={classnames(
																'form-control form-control-lg pay-info-input',
																{ 'is-invalid': this.state.formErrors.buy }
															)}
															id="buy"
															name="buy"
															type="number"
															placeholder="0.0"
														/>

														<div className="invalid-feedback">
															{this.state.formErrors.buy}
														</div>
													</div>
												</div>
												<button
													disabled={!this.state.buyValid}
													type="submit"
													className="pay-info-button"
													style={{ marginTop: '15px' }}
												>
													<svg
														stroke="currentColor"
														fill="currentColor"
														strokeWidth="0"
														viewBox="0 0 1024 1024"
														height="1em"
														width="1em"
														xmlns="http://www.w3.org/2000/svg"
													>
														<path d="M912 190h-69.9c-9.8 0-19.1 4.5-25.1 12.2L404.7 724.5 207 474a32 32 0 0 0-25.1-12.2H112c-6.7 0-10.4 7.7-6.3 12.9l273.9 347c12.8 16.2 37.4 16.2 50.3 0l488.4-618.9c4.1-5.1.4-12.8-6.3-12.8z" />
													</svg>
													<span className="ml-2">Buy</span>
												</button>
												<div style={{ marginTop: '40px' }}>
													<button
														disabled={tokenNameBtn}
														onClick={this.onwithRawTokenName}
														type="button"
														className="ant-btn ant-btn-default ant-btn-block ant-btn-dangerous pay-info-action-button"
													>
														<span>withdraw {window.localStorage.getItem('tokenName')}</span>
													</button>
												</div>
												<div style={{ marginTop: '10px' }}>
													<button
														disabled={netNameBtn}
														type="button"
														className="ant-btn ant-btn-default ant-btn-block ant-btn-dangerous pay-info-action-button"
														onClick={this.onwithdrawNetName}
													>
														<span>withdraw {netName}</span>
													</button>
												</div>
											</form>
										</div>
									</div>
								</div>
							</div>
						</div>
					</main>
				</section>
			</div>
		);
	}
}

PadInfo.propTypes = {
	getWhitelist: PropTypes.func.isRequired,
	pad: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
	pad: state.pad
});

export default connect(mapStateToProps, { getWhitelist })(PadInfo);
