import React, { Component } from "react";
import PropTypes from "prop-types";
import Card from "../common/Card";

class Pads extends Component {
  render() {
    const { pads } = this.props;

    return pads.map((pad) => (
      <Card
        presaleAddress={pad.presaleAddress}
        tokenAddress={pad.tokenAddress}
        title={pad.title}
        symbol={pad.symbol}
        decimal={pad.decimal}
        total={pad.total}
        rate={pad.rate}
        softCap={pad.softCap}
        hardCap={pad.hardCap}
        minBuy={pad.minBuy}
        maxBuy={pad.maxBuy}
        from={pad.from}
        to={pad.to}
        whitelistState={pad.whitelistState}
        whitelist={pad.whitelist}
        user={pad.user}
        chainID={pad.chainID}
        chainName={pad.chainName}
        logoUrl={pad.logoUrl}
        website={pad.website}
        facebook={pad.facebook}
        twitter={pad.twitter}
        github={pad.github}
        telegram={pad.telegram}
        instagram={pad.instagram}
        discord={pad.discord}
        reddit={pad.reddit}
        description={pad.description}
      />
    ));
  }
}

Pads.propTypes = {
  pads: PropTypes.array.isRequired,
};

export default Pads;
