import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Web3 from 'web3';
import { parse, stringify } from 'flatted';
import { IconButton, ButtonToolbar, Button } from 'rsuite';
import PauseIcon from '@rsuite/icons/PagePrevious';

class LaunchPad4 extends Component {
	constructor(props) {
		super(props);
		this.state = {
			presaleAddress: '',
			logoUrl: '',
			website: '',
			formErrors: { logoUrl: '', website: '' },
			logoUrlValid: false,
			websiteValid: false,
			formValid: false
		};

		this.onSubmit = this.onSubmit.bind(this);
	}

	onSubmit(e) {
		e.preventDefault();

		if (window.localStorage.getItem('signer')) {
			const signer = parse(window.localStorage.getItem('signer'));
			const userAddress = window.localStorage.getItem('userAddress');

			axios
				.get(`/api/getPresaleContractAbi`)
				.then(async (res) => {
					const abi = res.data;
					axios
						.get(`/api/getPresaleContractBytecode`)
						.then(async (res) => {
							const bytecode = res.data.object;
							const chainID = window.localStorage.getItem('chainId');

							const web3 = new Web3(Web3.givenProvider);
							const deploy_contract = new web3.eth.Contract(abi);

							var tokenFee = 0;
							var receiverAddress = '0x9CfC9B378B9CA8D835cC6BD85655BFa6BE252419';

							switch (chainID) {
								case '56':
									tokenFee = 0.7;
									break;
								case '97':
									tokenFee = 0.3;
									break;
								case '1':
									tokenFee = 0.1;
									break;
								case '3':
									tokenFee = 0.02;
									break;
								case '43114':
									tokenFee = 4.2;
									break;
								default:
									tokenFee = 0.7;
							}

							switch (chainID) {
								case 43114:
									receiverAddress = '0x78391Ff0E1Fc359Bc021dDB1E8aA47A62896A774';
									break;
								default:
									receiverAddress = '0x9CfC9B378B9CA8D835cC6BD85655BFa6BE252419';
							}

							let payload = {
								data: '0x' + bytecode,
								arguments: [
									window.localStorage.getItem('tokenAddress'),
									window.localStorage.getItem('presaleRate'),
									web3.utils.toWei(window.localStorage.getItem('minBuy')),
									web3.utils.toWei(window.localStorage.getItem('maxBuy')),
									web3.utils.toWei(window.localStorage.getItem('softCap')),
									web3.utils.toWei(window.localStorage.getItem('hardCap')),
									window.localStorage.getItem('fromTS'),
									window.localStorage.getItem('toTS'),
									receiverAddress,
									web3.utils.toWei(String(tokenFee), 'ether')
								]
							};

							let parameter = {
								from: userAddress,
								value: web3.utils.toWei(String(tokenFee), 'ether')
								// gas: web3.utils.toHex(5000000),
								// gasPrice: web3.utils.toHex(web3.utils.toWei("15", "gwei")),
							};

							deploy_contract
								.deploy(payload)
								.send(parameter, (err, transactionHash) => {
									console.log('Transaction Hash :', transactionHash);
								})
								.on('confirmation', () => {})
								.then((newContractInstance) => {
									console.log('Deployed Contract Address : ', newContractInstance.options.address);
									this.setState({
										presaleAddress: newContractInstance.options.address
									});
									window.localStorage.setItem('presaleAddress', newContractInstance.options.address);

									axios
										.post(`/api/addPresaleAddress`, {
											userAddress: userAddress,
											tokenAddress: window.localStorage.getItem('tokenAddress'),
											presaleAddress: newContractInstance.options.address,
											presaleRate: window.localStorage.getItem('presaleRate'),
											minBuy: window.localStorage.getItem('minBuy'),
											maxBuy: window.localStorage.getItem('maxBuy'),
											softCap: window.localStorage.getItem('softCap'),
											hardCap: window.localStorage.getItem('hardCap'),
											from: window.localStorage.getItem('from'),
											to: window.localStorage.getItem('to'),
											logoUrl: window.localStorage.getItem('logoUrl'),
											website: window.localStorage.getItem('website'),
											facebook: window.localStorage.getItem('facebook'),
											twitter: window.localStorage.getItem('twitter'),
											github: window.localStorage.getItem('github'),
											telegram: window.localStorage.getItem('telegram'),
											instagram: window.localStorage.getItem('instagram'),
											discord: window.localStorage.getItem('discord'),
											reddit: window.localStorage.getItem('reddit'),
											description: window.localStorage.getItem('description'),
											whiteliststat: window.localStorage.getItem('whiteliststate')
										})
										.then(() => {
											console.log('success');
											window.location.href = `/LaunchRes`;
										})
										.catch((err) => console.log(err));
								});
						})
						.catch((err) => console.log(err));
				})
				.catch((err) => console.log(err));
		} else {
			alert('You must connect a wallet!');
		}
	}

	render() {
		const presaleRate = window.localStorage.presaleRate;
		const softCap = window.localStorage.softCap;
		const hardCap = window.localStorage.hardCap;
		const minBuy = window.localStorage.minBuy;
		const maxBuy = window.localStorage.maxBuy;
		const liquidity = window.localStorage.liquidity;
		const from = window.localStorage.from;
		const to = window.localStorage.to;
		const liquidityLockTime = window.localStorage.liquidityLockTime;
		const website = window.localStorage.website;
		const tokenName = window.localStorage.tokenName;
		const tokenSymbol = window.localStorage.tokenSymbol;
		const tokenDecimals = window.localStorage.tokenDecimals;
		const tokenSupply = window.localStorage.tokenSupply;

		return (
			<section className="ant-layout black-background">
				<main className="ant-layout-content MainLayout_content__2mZF9">
					<div className="py-6 container">
						<div style={{ height: '16px' }} />
						<div className="bg-dark style-border ant-card ant-card-bordered">
							<div className="ant-card-body">
								<h1 className="socials text-center" style={{ fontSize: '50px' }}>
									Finish
								</h1>
								<p className="lead text-center" style={{ marginTop: '50px', fontSize: '18px' }}>
									<i>Review your information</i>
								</p>
								<form onSubmit={this.onSubmit} style={{ marginTop: '30px' }}>
									<div className="table-container">
										<div>
											<table>
												<tbody>
													<tr className="launch-tr">
														<td width="50%" className="launch-tr">
															Total token
														</td>
														<td className="has-text-primary has-text-right">
															{tokenSupply}
														</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Token name
														</td>
														<td className="has-text-info has-text-right">{tokenName}</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Token symbol
														</td>
														<td className="has-text-info has-text-right">{tokenSymbol}</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Token decimals
														</td>
														<td className="has-text-info has-text-right">
															{tokenDecimals}
														</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Presale rate
														</td>
														<td className="has-text-info has-text-right">{presaleRate}</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Softcap
														</td>
														<td className="has-text-info has-text-right">{softCap} BNB</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Hardcap
														</td>
														<td className="has-text-info has-text-right">{hardCap} BNB</td>
													</tr>

													<tr>
														<td width="50%" className="launch-tr">
															Minimum buy
														</td>
														<td className="has-text-info has-text-right">{minBuy} BNB</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Maximum buy
														</td>
														<td className="has-text-info has-text-right">{maxBuy} BNB</td>
													</tr>

													<tr>
														<td width="50%" className="launch-tr">
															Start time
														</td>
														<td className="has-text-info has-text-right">{from}</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															End time
														</td>
														<td className="has-text-info has-text-right">{to}</td>
													</tr>
													<tr>
														<td width="50%" className="launch-tr">
															Whitelist
														</td>
														<td className="has-text-info has-text-right">
															{window.localStorage.getItem('whiteliststate')}
														</td>
													</tr>

													<tr>
														<td width="50%" className="launch-tr">
															Website
														</td>
														<td className="has-text-info has-text-right">{website}</td>
													</tr>
												</tbody>
											</table>
										</div>
									</div>

									<ButtonToolbar>
										<Link to="/LaunchPad3">
											<IconButton
												icon={<PauseIcon className="icon" />}
												placement="left"
												id="launch-tool-button"
											>
												<strong>Back</strong>
											</IconButton>
										</Link>

										<Button type="submit" id="launch-tool-button">
											<strong>Submit</strong>
										</Button>
									</ButtonToolbar>
								</form>
							</div>
						</div>
					</div>
				</main>
			</section>
		);
	}
}

LaunchPad4.propTypes = {
	// verify: PropTypes.string.isRequired
};

const mapStateToProps = (state) => ({
	presaleAddress: state.presaleAddress
	// errors: state.errors
});

export default connect(mapStateToProps, {})(LaunchPad4);
