import React from "react";
import { Link } from "react-router-dom";
import { Button } from "rsuite";

export default function Dashboard() {
  return (
    <section className="ant-layout black-background">
      <main
        className="ant-layout-content MainLayout_content__2mZF9"
        style={{ fontSize: "20px" }}
      >
        <div className="py-6 container">
          <div style={{ height: "16px" }}></div>

          <div
            className="bg-dark style-border ant-card ant-card-bordered"
            style={{ marginBottom: "100px" }}
          >
            <div className="ant-card-body">
              <h3
                className="lead text-center socials"
                style={{ fontSize: "40px" }}
              >
                SaFuTrendzPad the Evolution of SaFu Projects
              </h3>
              <p className="lead text-center" style={{ marginTop: "50px" }}>
                <i style={{ fontSize: "20px" }}>
                  SaFuTrendzPad gives you the best secure, fast, and reliable
                  launchpad for your projects which all token needs to pass
                  through a verification service before getting listed on our
                  platform. We do this to create trust and secure trading for
                  our investors, everyone can trade with no risk.
                </i>
              </p>
            </div>
          </div>
          <h3 className="text-center socials" style={{ fontSize: "30px" }}>
            SaFuTrendzPad Ecosystem
          </h3>
          <p className="lead text-center">
            <i>
              Suitable tools to get started with our launchpad, with fast,
              efficiency, and support towards your project and 100%
              decentralized.
            </i>
          </p>
          <div style={{ marginTop: "70px" }}>
            <div
              className=" bg-dark style-border ant-card ant-card-bordered"
              style={{ marginBottom: "70px" }}
            >
              <div className="ant-card-body">
                <h1
                  className="lead text-center socials"
                  style={{ fontSize: "30px" }}
                >
                  Mint Token
                </h1>
                <p className="lead text-center" style={{ fontSize: "20px" }}>
                  <i>Mint standard tokens on ETH, BSC, AVAX, Cronos.</i>
                </p>
                <div
                  className="has-text-centered"
                  style={{ marginTop: "30px" }}
                >
                  <Link to="/CreateToken">
                    <Button className="db-button">
                      <strong>Create Now</strong>
                    </Button>
                  </Link>
                </div>
              </div>
            </div>
            <div
              className="bg-dark style-border ant-card ant-card-bordered"
              style={{ marginBottom: "70px" }}
            >
              <div className="ant-card-body">
                <h1
                  className="lead text-center socials"
                  style={{ fontSize: "30px" }}
                >
                  Launchpad
                </h1>
                <p className="lead text-center" style={{ fontSize: "20px" }}>
                  <i>
                    Use the token you mint to create a launchpad with just a few
                    clicks.
                  </i>
                </p>
                <div
                  className="has-text-centered"
                  style={{ marginTop: "30px" }}
                >
                  <Link to="/LaunchPad1">
                    <Button className="db-button">
                      <strong>Launch Now</strong>
                    </Button>
                  </Link>
                </div>
              </div>
            </div>
            <div
              className="bg-dark style-border ant-card ant-card-bordered"
              style={{ marginBottom: "50px" }}
            >
              <div className="ant-card-body">
                <h1
                  className="lead text-center socials"
                  style={{ fontSize: "30px" }}
                >
                  Airdrop
                </h1>
                <p className="lead text-center" style={{ fontSize: "20px" }}>
                  <i>
                    Distribute Airdrop / Giveaway with a few clicks Supports
                    only BSC Token, More chains coming soon on our Airdrop DAPP
                  </i>
                </p>
                <div
                  className="has-text-centered"
                  style={{ marginTop: "30px" }}
                >
                  <a href="https://multisender.safutrendz.com/">
                    <Button className="db-button">
                      <strong>Go Now</strong>
                    </Button>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </section>
  );
}
