import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter, Link } from "react-router-dom";
import classnames from "classnames";
import isEmpty from "../../validation/isEmpty";
import { IconButton, ButtonToolbar, Checkbox } from "rsuite";
import PauseIcon from "@rsuite/icons/PagePrevious";
import PlayIcon from "@rsuite/icons/PageNext";

var valNetState = "";
var valWhitelistState=false;
class LaunchPad2 extends Component {
  constructor(props) {
    super(props);

    this.changeWhitelise=this.changeWhitelise.bind(this);
    this.state = {
      presaleRate: undefined,
      // optradio: false,
      softCap: undefined,
      hardCap: undefined,
      minBuy: undefined,
      maxBuy: undefined,
      // liquidity: 0,
      // listingRate: 0,
      // router: '',

      from: "",
      to: "",
      // liquidityLockTime: 0,
      // vestContributor: false,
      // teamVesting: false,
      formErrors: {
        presaleRate: "",
        softCap: "",
        hardCap: "",
        minBuy: "",
        maxBuy: "",
        // liquidity: 0,
        // listingRate: 0,
        // liquidityLockTime: 0,
        from: "",
        to: "",
        // router: ''
      },
      presaleRateValid: false,
      softCapValid: false,
      hardCapValid: false,
      minBuyValid: false,
      maxBuyValid: false,
      // liquidityValid: false,
      // listingRateValid: false,
      fromValid: false,
      toValid: false,
      // routerValid: false,
      // liquidityLockTimeValid: false,

      formValid: false,
      isChecked:false
    };
  }

  handleInput(e) {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ [name]: value }, () => {
      this.validateField(name, value);
    });
    if (e.target.name == "to" || e.target.name == "from") {
      const dateX = new Date(e.target.value);
      window.localStorage.setItem(name + "TS", dateX.getTime() / 1000);
    }
    window.localStorage.setItem(name, value);
  }

  validateField(fieldName, value) {
    let fieldValidationErrors = this.state.formErrors;

    let presaleRateValid = this.state.presaleRateValid;
    let softCapValid = this.state.softCapValid;
    let hardCapValid = this.state.hardCapValid;
    let minBuyValid = this.state.minBuyValid;
    let maxBuyValid = this.state.maxBuyValid;
    // let liquidityValid = this.state.liquidityValid;
    // let listingRateValid = this.state.listingRateValid;
    let fromValid = this.state.fromValid;
    let toValid = this.state.toValid;
    // let routerValid = this.state.routerValid;
    // let liquidityLockTimeValid = this.state.liquidityLockTimeValid;

    switch (fieldName) {
      case "presaleRate":
        presaleRateValid = value > 0;
        fieldValidationErrors.presaleRate = presaleRateValid
          ? ""
          : " is invalid";
        break;
      case "softCap":
        softCapValid =
          Number(value) >= Number(this.state.hardCap) / 4 &&
          Number(value) < Number(this.state.hardCap);
        fieldValidationErrors.softCap = softCapValid ? "" : " is invalid";
        break;
      case "hardCap":
        hardCapValid = value > 0;
        fieldValidationErrors.hardCap = hardCapValid ? "" : " is invalid";
        break;
      case "minBuy":
        minBuyValid = value > 0;
        fieldValidationErrors.minBuy = minBuyValid ? "" : " is invalid";
        break;
      case "maxBuy":
        maxBuyValid = value > 0;
        fieldValidationErrors.maxBuy = maxBuyValid ? "" : " is invalid";
        break;
      // case 'liquidity':
      //     liquidityValid =  value >= 50;
      //     fieldValidationErrors.liquidity = liquidityValid ? '' : ' is invalid';
      //     break;
      // case 'listingRate':
      //     listingRateValid =  value > 0;
      //     fieldValidationErrors.listingRate = listingRateValid ? '' : ' is invalid';
      //     break;
      case "from":
        fromValid = isEmpty(value) ? "" : "have value";
        fieldValidationErrors.from = fromValid ? "" : "must have start time.";
        break;
      case "to":
        toValid = isEmpty(value) ? "" : "have value";
        fieldValidationErrors.to = toValid ? "" : "must have end time.";
        fieldValidationErrors.to =
          this.state.to > this.state.from
            ? ""
            : "end time must be after start time.";

        break;
      // case 'router':
      //     routerValid = value == '---Select Router Exchange---' ? '' : 'select router';
      //     fieldValidationErrors.router = routerValid ? '' : 'select router';
      //     break;
      // case 'liquidityLockTime':
      //     liquidityLockTimeValid =  value >= 5;
      //     fieldValidationErrors.liquidityLockTime = liquidityLockTimeValid ? '' : ' is invalid';
      //     break;
      default:
        break;
    }
    this.setState(
      {
        formErrors: fieldValidationErrors,
        presaleRateValid: presaleRateValid,
        softCapValid: softCapValid,
        hardCapValid: hardCapValid,
        minBuyValid: minBuyValid,
        maxBuyValid: maxBuyValid,
        // liquidityValid: liquidityValid,
        // listingRateValid: listingRateValid,
        // liquidityLockTimeValid: liquidityLockTimeValid,
        // routerValid: routerValid,
        fromValid: fromValid,
        toValid: toValid,
      },
      this.validateForm
    );
  }

  validateForm() {
    this.setState({
      formValid:
        this.state.presaleRateValid &&
        this.state.softCapValid &&
        this.state.hardCapValid &&
        this.state.minBuyValid &&
        this.state.maxBuyValid,
    });
  }

  changeWhitelise(){
    this.setState({
      isChecked: !this.state.isChecked,
    });
    console.log(this.state.isChecked)
    window.localStorage.setItem("whiteliststate",!this.state.isChecked);
  }

  render() {
    switch (window.localStorage.getItem("chainId")) {
      case "1": valNetState="ETH"; break;
      case "3": valNetState="ETH"; break;
      case "56": valNetState="BNB"; break;
      case "97": valNetState="tBNB"; break;
      case "43114": valNetState="AVAX"; break;
      default: valNetState="BNB"; break;   
    }

    return (
      <>
        <section className="ant-layout  black-background">
          <main className="ant-layout-content MainLayout_content__2mZF9">
            <div className="py-6 container">
              <div style={{ height: "16px" }}></div>
              <div className="bg-dark style-border  ant-card ant-card-bordered">
                <div className="ant-card-body">
                  <h1
                    className="socials text-center"
                    style={{ fontSize: "40px" }}
                  >
                    Launchpad Info
                  </h1>
                  <p
                    className="lead text-center"
                    style={{ marginTop: "40px", fontSize: "20px" }}
                  >
                    <i>
                      Enter the launchpad information that you want to raise ,
                      that should be enter all details about your presale
                    </i>
                  </p>

                  <form>
                    <div className="field">
                      <label className="launch-label" htmlFor="presaleRate">
                        Presale rate
                      </label>
                      <div className="control">
                        <input
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.presaleRate,
                            }
                          )}
                          type="number"
                          placeholder="100"
                          id="tokenAddress"
                          name="presaleRate"
                          autoComplete="on"
                          value={this.state.presaleRate}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.presaleRate}
                        </div>

                        <p className="help">
                          If I spend 1 {valNetState} how many tokens will I
                          receive?
                        </p>
                      </div>
                    </div>

                    <div className="field">
                      <label className="launch-label" htmlFor="softCap">
                        Softcap ({valNetState})
                      </label>
                      <div className="control">
                        <input
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.softCap,
                            }
                          )}
                          type="number"
                          placeholder="Ex: 10"
                          id="tokenAddress"
                          name="softCap"
                          autoComplete="on"
                          value={this.state.softCap}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.softCap}
                        </div>
                        <p className="help">
                          Softcap must be &gt;= 25% of Hardcap!
                        </p>
                      </div>
                    </div>

                    <div className="field">
                      <label className="launch-label" htmlFor="hardCap">
                        HardCap ({valNetState})
                      </label>
                      <div className="control">
                        <input
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.hardCap,
                            }
                          )}
                          type="number"
                          placeholder="Ex: 10"
                          id="tokenAddress"
                          name="hardCap"
                          autoComplete="on"
                          value={this.state.hardCap}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.hardCap}
                        </div>
                      </div>
                    </div>

                    <div className="field">
                      <label className="launch-label" htmlFor="minBuy">
                        Minimum buy ({valNetState})
                      </label>
                      <div className="control">
                        <input
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.minBuy,
                            }
                          )}
                          type="number"
                          placeholder={valNetState}
                          id="tokenAddress"
                          name="minBuy"
                          autoComplete="on"
                          value={this.state.minBuy}
                          onChange={(event) => this.handleInput(event)}
                        />
                      </div>
                      <div className="invalid-feedback">
                        {this.state.formErrors.minBuy}
                      </div>
                    </div>

                    <div className="field">
                      <label className="launch-label" htmlFor="maxBuy">
                        Maximum buy ({valNetState})
                      </label>
                      <div className="control">
                        <input
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.maxBuy,
                            }
                          )}
                          type="number"
                          placeholder={valNetState}
                          id="tokenAddress"
                          name="maxBuy"
                          autoComplete="on"
                          value={this.state.maxBuy}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.maxBuy}
                        </div>
                      </div>
                    </div>
                    <Checkbox
                      className="launch-label"
                      style={{ marginTop: "70px", marginLeft: "-45%" }}
                      defaultChecked={this.state.isChecked}
                       onChange={this.changeWhitelise}
                    >
                      Whitelist
                    </Checkbox>

                    <div className="field" style={{ marginTop: "70px" }}>
                      <label className="launch-label" htmlFor="startTime">
                        Start time (UTC)
                      </label>
                      <div>
                        <input
                          id="launch-month"
                          type="date"
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.from,
                            }
                          )}
                          name="from"
                          value={this.state.from}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.from}
                        </div>
                      </div>
                    </div>
                    <div className="field" style={{ marginTop: "70px" }}>
                      <label className="launch-label" htmlFor="endTime">
                        End time (UTC)
                      </label>
                      <div>
                        <input
                          id="launch-month"
                          className={classnames(
                            "form-control form-control-lg",
                            {
                              "is-invalid": this.state.formErrors.to,
                            }
                          )}
                          name="to"
                          type="date"
                          value={this.state.to}
                          onChange={(event) => this.handleInput(event)}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.to}
                        </div>
                      </div>
                    </div>
                    <div className="has-text-centered">
                      <div className="has-text-info p-4">
                        Need {this.state.hardCap * this.state.presaleRate}{" "}
                        {window.localStorage.getItem("tokenSymbol")} to create
                        launchpad.
                      </div>
                      <ButtonToolbar>
                        <Link to="/LaunchPad1">
                          <IconButton
                            icon={<PauseIcon className="icon" />}
                            placement="left"
                            id="launch-tool-button"
                          >
                            <strong>Back</strong>
                          </IconButton>
                        </Link>
                        <Link to={this.state.formValid ? "/LaunchPad3" : "#"}>
                          <IconButton
                            icon={<PlayIcon className="icon" />}
                            placement="right"
                            id="launch-tool-button"
                          >
                            <strong>Next</strong>
                          </IconButton>
                        </Link>
                      </ButtonToolbar>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </main>
        </section>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    tokenAddress: state.verify.tokenAddress,
  };
};

export default connect(mapStateToProps)(LaunchPad2);
